"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Box, List, ListItem, ListItemButton, ListItemText, ListItemIcon } from "@mui/material";
import { signOut } from "next-auth/react";

function SideMenuContent({ tab }) {
  const router = useRouter();
  const [labels, setLabels] = useState([]);

  const handleTab = (tab) => {
    if (tab === "projects") {
      return ["Editar", "Actividad reciente", "Colaboradores", "Nueva tarea", "Configuraci√≥n"];
    } else if (tab === "social") {
      return ["Amigos", "Grupos", "Publicaciones", "Configuraci√≥n"];
    } else if (tab === "notifications") {
      return ["Notificaciones", "Configuraci√≥n"];
    } else if (tab === "profile") {
      return ["Editar Informaci√≥n", "Configuraci√≥n", "Cerrar sesi√≥n"];
    }
  };

  useEffect(() => {
    setLabels(handleTab(tab));
  }, [tab]);

  const handleClick = (label) => {
    switch (label) {
      case "Editar":
        console.log("Redirigiendo a edici√≥n...");
        router.push("/edit"); // Cambia la ruta seg√∫n tu configuraci√≥n
        break;
      case "Actividad reciente":
        console.log("Mostrando actividad reciente...");
        break;
      case "Colaboradores":
        console.log("Abriendo lista de colaboradores...");
        break;
      case "Nueva tarea":
        console.log("Creando nueva tarea...");
        break;
      case "Configuraci√≥n":
        console.log("Redirigiendo a configuraci√≥n...");
        router.push("/settings");
        break;
      case "Amigos":
        console.log("Mostrando amigos...");
        break;
      case "Cerrar sesi√≥n":
        console.log("Cerrando sesi√≥n...");
        signOut({ callbackUrl: "/" });
        break;
      default:
        console.log(`Acci√≥n no definida para: ${label}`);
    }
  };

  return (
    <div className="slideMenu">
      <Box sx={{ width: "100%", maxWidth: 360, bgcolor: "background.paper" }}>
        <nav aria-label="main mailbox folders">
          <List>
            {labels.map((label, index) => (
              <ListItem key={index} disablePadding>
                <ListItemButton onClick={() => handleClick(label)}>
                  <ListItemIcon>{/* Puedes agregar √≠conos aqu√≠ */}</ListItemIcon>
                  <ListItemText primary={label} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        </nav>
      </Box>
    </div>
  );
}

export default SideMenuContent;